import React from 'react'
import { toast } from "react-toastify";
import MainHeader from 'views/mobileView/components/molecules/Headers/MainHeader'
import cssVar from 'views/mobileView/styles'
import db from 'libs/db'
import { AiOutlineDown, AiOutlineQuestionCircle } from 'react-icons/ai';
import Modal from 'react-bootstrap/Modal';
import { FaBalanceScaleRight } from 'react-icons/fa';
import PerformanceChart from 'views/mobileView/components/molecules/Cards/PerformanceChart';
import { BsGraphUp } from 'react-icons/bs';

const RebalancingStrategy = (props) => {
    const [rebalanceFrequency, setRebalanceFrequency] = React.useState(1)
    const [selectedETFs, setSelectedETFs] = React.useState([])
    const [showModal, setShowModal] = React.useState(false)
    const [showNeverRebalance, setShowNeverRebalance] = React.useState(false)
    const [showHistoricalModal, setShowHistoricalModal] = React.useState(false)
    const minInv = props.location.state.minInv;
    const options = [
        { key: 1, text: "Every 3 months" },
        { key: 2, text: "Every 6 months" },
        { key: 3, text: "Every 12 months" },
        { key: 4, text: <span>Rebalance manually <AiOutlineQuestionCircle size={20} className='secondaryGreen' onClick={()=>setShowNeverRebalance(true)}/></span> }
    ]
    React.useEffect(() => {
        loadData();
    }, []);
    React.useEffect(() => {
        saveData(rebalanceFrequency)
    }, [rebalanceFrequency]);

    const loadData = async () => {
        try {
            await db.get(db.model.createMintbox).then((u) => {
                if (u?.selectedETFs) {
                    setSelectedETFs(u.selectedETFs);
                }
                if (u?.rebalanceFrequency) {
                    setRebalanceFrequency(u.rebalanceFrequency)
                }
            })
        } catch (error) {
            toast.error(error);
        }
    };

    const saveData = async (r) => {
        const dataFinal = {
            rebalanceFrequency: r
        };
        await db.get(db.model.createMintbox).then(async (u) => {
            if (u) { Object.assign(u, dataFinal) }
            else { u = dataFinal }
            await db.set(db.model.createMintbox,JSON.stringify(u));
        })
    }


    const RebalancingModal = () => {
		return (
			<Modal
				className="bottom-drawer"
				show={showModal}
				onHide={() => setShowModal(false)}
				keyboard={false}
			>
				<Modal.Body>
                    <div style={{ paddingBottom: "4%", display:'flex',gap:'4%' }} className='header2dark'>
                        <FaBalanceScaleRight size={20}/>
                        <div>Understanding rebalancing</div>
                    </div>
					<div style={{ paddingBottom: "4%" }} className='bodyTextDark'>
                        Over time, the weights of the constituents in your mintbox can change. This happens because different constituents grow at different rates. By rebalancing your mintbox you can realign the weights to the values selected by you during your mintbox’s setup
                    </div>
				</Modal.Body>
				<Modal.Footer>
					<button className="solidButton" onClick={() => {
						setShowModal(false);
					}}>
						Close
					</button>
				</Modal.Footer>
			</Modal>
		)
	}
    
    const NeverRebalanceModal = () => {
		return (
			<Modal
				className="bottom-drawer"
				show={showNeverRebalance}
				onHide={() => setShowNeverRebalance(false)}
				keyboard={false}
			>
				<Modal.Body>
                    <div style={{ paddingBottom: "4%" }} className='bodyTextDark'>
                        By selecting this option, you will not receive periodic rebalancing updates for this mintbox. However, you will be able to rebalance this mintbox manually whenever you wish to from your portfolio.
                    </div>
				</Modal.Body>
				<Modal.Footer>
					<button className="solidButton" onClick={() => {
						setShowNeverRebalance(false);
					}}>
						Close
					</button>
				</Modal.Footer>
			</Modal>
		)
	}

    const HistoricalModal = () => {
        return (
            <Modal
                className="bottom-drawer"
                show={showHistoricalModal}
                onHide={() => setShowHistoricalModal(false)}
                keyboard={false}
            >
                <Modal.Body style={{ maxHeight: "80vh", overflow: "scroll",padding:"0" }}>
                    <PerformanceChart etfs={selectedETFs} type={4} fundCode={1} rebalanceFreq={rebalanceFrequency===1?'Q':rebalanceFrequency===2? '6M': rebalanceFrequency === 3? 'Y': rebalanceFrequency===4? 'False': 'Q'} />
                </Modal.Body>
                <Modal.Footer>
                    <button className="outlineButton" onClick={() => setShowHistoricalModal(false)}>
                        Close
                    </button>
                </Modal.Footer>
            </Modal>
        )
    }

    const Footer = () => {
        return (
            <>
                <div style={{ position: "fixed", bottom: '70px', width: "100%",backgroundColor: cssVar.getPropertyValue('--white'), textAlign: "center" }}>
                    <div className="bodyTextDark secondaryGreen" onClick={() => setShowHistoricalModal(true)} style={{ borderTop: "1px solid "+cssVar.getPropertyValue('--light'), padding: "3% 4%"}}>
                        Historical performance <BsGraphUp size={14} style={{ margin: "5px" }} />
                    </div>
                    <div className="bodyTextLight" style={{ borderTop: "1px solid "+cssVar.getPropertyValue('--light'), padding: "2% 4%"}}>
                        Min investment <b style={{ color: "#333333" }}>₹ {Number(minInv).toFixed(2)}</b>
                    </div>
                </div>
                <div className='footer'>
                    <button
                        className='solidButton'
                        onClick={() => { props.history.push({ pathname: '/CreateMintbox/name-mintbox', state: props.location.state }) }}
                    >
                        Bring your mintbox to life
                    </button>
                </div>
            </>
        )
    }
    
    return (
        <div className='mainContainer'>
            <MainHeader
                header="Build your own mintbox"
                navigation={props.history}
            />
            <div className="dataContainer" style={{ paddingBottom: "120px", paddingTop:"2%"  }}>
                <div className='header1dark' style={{marginTop:"4%"}}>
                    How often would you like to rebalance your mintbox?
                </div>
                <div className='header2dark' style={{ color: cssVar.getPropertyValue("--secondaryGreen"),marginTop:"1%"}} onClick={()=>setShowModal(true)}>
                    What is rebalancing? <AiOutlineDown size={12} />
                </div>
                {
                    options.map((option) => {
                        return (
                            <div className="radio-item" key={option.key} style={{ paddingTop: "24px" }}>
                                <input type="radio" id={option.key} name="rebalanceFrequency" className="textNormal"
                                    onChange={(e) => { setRebalanceFrequency(parseInt(e.target.value)) }} value={option.key}
                                    checked={option.key === rebalanceFrequency} />
                                <label className="radio-label" htmlFor={option.key}>
                                    <div className='bodyTextDark'>{option.text}
                                    </div>
                                </label>
                            </div>
                        )
                    })
                }
            </div>
            <Footer />
            <HistoricalModal />
            <RebalancingModal />
            <NeverRebalanceModal />
        </div>
    )
}

export default RebalancingStrategy
